@import "../../App";

.event-edit-form {
  background: $white;
  width: 200px;
  position: absolute;
  right: 30px;
  top: 50px;
  border: 1px solid $gray-muted;
  border-radius: 5px;
  padding: 15px 10px;
  z-index: 2;

  &:before {
    content: '';
    top: -6px;
    width: 10px;
    height: 10px;
    right: 27px;
    transform: rotate(45deg);
    background: $white;
    position: absolute;
    border: 1px solid $gray-muted;
    border-bottom: none;
    border-right: none;
  }

  .subtitle {
    font-family: 'Montserrat Regular', sans-serif;
    margin-bottom: 15px;
    letter-spacing: .5px;
  }

  .form-input {
    display: flex;
    flex-direction: column;
    margin-bottom: 15px;

    &.row {
      flex-direction: row;
      align-items: center;
    }

    input,
    span,
    label {
      font-family: 'Montserrat Regular', sans-serif;
      padding: 5px;
      font-size: 12px;
      color: $gray-muted;
    }

    .date-form {
      border: 1px solid $gray-muted;
      border-radius: 5px;
    }
  }
}